	// import common from "../../common/common.js";
	// 导入自定义组件
	import helloComp from "../../components/helloComp.vue";
	import trailerStars from "../../components/trailerStars.vue"
	export default {
		data() {
			return {
				title: 'Hello',
				carouselList: [],
				hotSuperheroList: [],
				hotTrailerList: [],
				animationData:{},
				guessULikeList: [],
				animationDataArr: [
					{}, {}, {}, {}, {}
				]
				
			}
		},
		
		onUnload() {
// 页面卸载的时候，清除动画数据
			this.animationData = {};
			this.animationDataArr = [
				{}, {}, {}, {}, {}
			];
		},
		onPullDownRefresh() {
			this.refresh();
		},
		onLoad() {
			var me = this;
			




			// 获取common.js 服务器中的url
			// var serverUrl = common.serverUrl;
			// 通过挂载到main.js 中获取服务器的地址，作为全局变量
			var serverUrl = me.serverUrl;
			// 查询热门超英
			uni.request({
				// 请求轮播图数据
				url: serverUrl + '/index/carousel/list', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {

					console.log("请求轮播图数据");
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var carouselList = res.data.data;
						me.carouselList = carouselList;
					}
				}
			});
			uni.request({
				// 请求超英
				url: serverUrl + '/index/movie/hot?type=superhero', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {

					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotSuperheroList = res.data.data;
						me.hotSuperheroList = hotSuperheroList;
					}
				}
			});
			uni.request({
				// 请求热门
				url: serverUrl + '/index/movie/hot?type=trailer', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {
				
					console.log("请求热门");
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotTrailerList = res.data.data;
						me.hotTrailerList = hotTrailerList;
					}
				}
			});uni.request({
				// 请求轮播图数据
				url: serverUrl + '/index/movie/hot?type=trailer', //仅为示例，并非真实接口地址。
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: '2573971154'
				},
				success: (res) => {
					
					console.log(res);
					// 获取真是数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						var hotTrailerList = res.data.data;
						me.hotTrailerList = hotTrailerList;
					}
				}
			});
		
				this.refresh();
		},
		methods: {
			refresh() {
				var me = this;
				
				uni.showLoading({
					mask: true
				});
				uni.showNavigationBarLoading();
				
				
				var serverUrl = me.serverUrl;
				// 查询猜你喜欢数据列表
				uni.request({
					url: serverUrl + '/index/guessULike',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						qq: '2573971154'
					},
					method: "POST",
					success: (res) => {
						// 获取真实数据之前，务必判断状态是否为200
						console.log(res.data)
						if (res.data.status == 200) {
							var guessULikeList = res.data.data;
							me.guessULikeList = guessULikeList;
						}
					},
					complete: () => {
						uni.hideNavigationBarLoading();
						uni.hideLoading();
						uni.stopPullDownRefresh();
					}
				});
			},
			// 实现点赞动画效果
			praiseMe(e) {























			}
		},
		components:{
			helloComp,
			trailerStars
		}
	}
